html body,
html .card-header-title .tag {
  font-family: 'Open Sans', sans-serif;
}

h1, h2, h3, h4, h5, h6, .title, .card-header-title {
  font-family: 'Quicksand', sans-serif;
}

.App button, .App input, .App select, .App textarea {
  font-family: 'Open Sans', sans-serif;
}

.tag {
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
}

.tags.map-tooltip {
  position: absolute;
  z-index: 2;
  pointer-events: none;
}

.tags.map-tooltip .tag {
  margin-bottom: 0;
  margin-top: 0;
}

.project .title:first-child {
  margin-bottom: 0.5em;
}

.project--section:not(:last-child) {
  margin-bottom: 1em;
}

.content ul.project-steps {
  margin-top: 0;
}

.project .card-content,
.project .card-header {
  transition: box-shadow 50ms ease-in-out, background-color 100ms ease-in-out;
}

.project:hover {
  box-shadow: 0 2px 3px rgba(0, 209, 178, .1), 0 0 0 1px rgba(0, 209, 178, 1);
}

.project:hover .card-header,
.project.active .card-header {
  box-shadow: 0 1px 2px rgba(0, 209, 178, .1), 0 0 0 1px rgba(0, 209, 178, 1);
  background-color: rgba(0, 209, 178, .25);
}

.project::after {
  content: 'Click to learn more';
  position: absolute;
  right: 0.5em;
  font-size: 0.8em;
  bottom: 0.2em;
  font-weight: 600;
  opacity: 0;
  transition: 50ms ease-in-out opacity;
}

.project:hover .card-content {
  background-color: rgba(0, 209, 178, .1);
}

.project:not(.active):hover::after {
  opacity: 1;
}

.project-enter {
  opacity: 0.01;
}

.project-enter.project-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}

.project-leave {
  opacity: 1;
}

.project-leave.project-leave-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}
